export default {
  members(state) {
    return state.members;
  },
  currentUser(state) {
    return state.currentMember;
  },
  hasMembers(state) {
    return state.members && state.members.length > 0;
  },
  isMember(_, getters, _2, rootGetters) {
    const members = getters.members;
    const userId = rootGetters.userID;
    return members.some((member) => member.id === userId);
  },
};
