        velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint
        occaecat cupidatat non proident, sunt in culpa qui officia deserunt
        mollit anim id est laborum.
      </div>
    </div>
    <h3 class="text-center my-3">Workshops</h3>

    <div class="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-5">
      <div v-for="post in posts" :key="post.id" class="col">
        <router-link :to="'/workshops/' + post.uid">
          <div class="card text-dark bg-light h-100 card-hover">
            <img :src="post.data.course_image.url" class="card-img" alt="..." />
            <div class="card-body">
              <h5 class="card-title">{{ post.data.course_title[0].text }}</h5>
              <p v-snip="3" class="card-text">
                {{ post.data.course_description[0].text }}
              </p>
            </div>
            <div class="card-footer bg-transparent">
              <div class="d-flex justify-content-between">
                <small class="text-end text-capitalize">{{ convertDate(post.data.start_date) }}</small>

                <small
                  v-if="post.data.online_or_offline === 'online'"
                  class="text-end badge rounded-pill bg-warning text-dark"
                >
                  <i class="bi bi-camera-video-fill mx-2"></i>

                  Online</small
                >

                <small v-else class="text-end badge rounded-pill bg-success">
                  <i class="bi bi-people-fill mx-2"></i>

                  In person</small
                >
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "WorkshopList",
  data() {
    return {
      posts: [],
      isLoading: false,
    };
  },
  methods: {
    getPosts() {
      this.isLoading = true;
      //Query to get blog posts
      this.$prismic.client
        .query(this.$prismic.Predicates.at("document.type", "courses"), {
          orderings: "[document.first_publication_date desc]",
        })
        .then((response) => {
          this.posts = response.results;
          this.textLoaded = false;
          this.isLoading = false;
        });
    },
    convertDate(el) {
      let date = new Date(el + "T00:00:00");
          let options = {
            year: "numeric",
            month: "long",
            day: "numeric"
          };
          return date.toLocaleString("ru-UK", options);
     
    }
  },
  created() {
    this.getPosts();
  },
};
</script>

