import { createStore } from "vuex";
import authModule from "@/store/modules/auth/index.js";
import membersModules from "@/store/modules/members/index.js";

export default createStore({
  modules: {
    auth: authModule,
    members: membersModules,
  },
  state() {
    return {
      userId: "m4",
    };
  },
  getters: {
    userID(state) {
      return state.userId;
    },
  },
  mutations: {
    registerCoach(state, payload) {
      state.coaches.push(payload);
    },
  },
  actions: {
    registerCoach(context, data) {
      const coachData = {
        id: "c2",
        firstName: data.first,
        lastName: data.last,
        email: data.email,
      };
      context.commit("registeredCoach", coachData);
    },
  },
});
