<template>
  <div>
    <LoadingSpinner :show="isLoading" />
    <ErrorModal :error-text="error" :show="!!error" @click="handleError" />

    <div
      class="auth-wrapper d-flex align-items-center justify-content-center m-3"
    >
      <div
        class="
          auth-card
          col col-sm-8 col-md-6 col-lg-4
          text-dark
          bg-light
          shadow
          p-3
        "
      >
        <form @submit.prevent="submitForm">
          <div v-if="mode !== 'signup-info'">
            <div class="mb-3">
              <!-- Email -->

              <input
                placeholder="Email Address*"
                class="form-control"
                type="email"
                id="email"
                v-model.trim="email"
              />
            </div>
            <!-- Password -->

            <div class="mb-3">
              <input
                placeholder="Password*"
                class="form-control"
                type="password"
                id="password"
                v-model.trim="password"
              />
            </div>
          </div>
          <!-- First Name -->
          <div v-if="mode === 'signup-info'">
            <div class="mb-3">
              <input
                placeholder="Firstname"
                @blur="clearValidity('firstName')"
                class="form-control"
                type="text"
                id="firstname"
                v-model.trim="firstName.val"
              />
              <p class="form-text" v-if="!firstName.isValid">
                First Name must not be empty.
              </p>
            </div>

            <!-- Last Name -->
            <div class="mb-3">
              <input
                placeholder="Surname"
                @blur="clearValidity('lastName')"
                class="form-control"
                type="text"
                id="lastname"
                v-model.trim="lastName.val"
              />
              <p class="form-text" v-if="!lastName.isValid">
                Last Name must not be empty.
              </p>
            </div>
          </div>

          <p class="form-text" v-if="!formIsValid">
            Form is invalid. Please try again.
          </p>
          <button class="btn btn-outline-secondary">
            {{ sumbitButtonCaption }}
          </button>
          <button
            v-if="mode === 'login' || mode === 'signup'"
            type="button"
            class="btn btn-dark mx-2"
            mode="flat"
            @click="switchAuthMode"
          >
            {{ switchModeButtonCaption }}
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      firstName: {
        val: "",
        isValid: true,
      },
      lastName: {
        val: "",
        isValid: true,
      },
      email: "",
      password: "",
      formIsValid: true,
      mode: "login",
      isLoading: false,
      error: null,
    };
  },
  computed: {
    sumbitButtonCaption() {
      if (this.mode === "login") {
        return "Login";
      } else if (this.mode === "signup") {
        return "Signup";
      } else {
        return "Save";
      }
    },
    switchModeButtonCaption() {
      if (this.mode === "login") {
        return "Signup instead";
      } else {
        return "Login instead";
      }
    },
  },
  methods: {
    clearValidity(input) {
      this[input].isValid = true;
    },
    validateForm() {
      if (this.firstName.val === "") {
        this.firstName.isValid = false;
        this.formIsValid = false;
        return;
      }
      if (this.lastName.val === "") {
        this.lastName.isValid = false;
        this.formIsValid = false;
        return;
      }
    },
    async submitForm() {
      this.formIsValid = true;
      if (this.password.length < 6) {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;

      const formData = {
        email: this.email,
        password: this.password,
      };

      try {
        // Login
        if (this.mode === "login") {
          await this.$store.dispatch("login", formData);
          this.$router.replace("/");
          // Signup
        } else if (this.mode === "signup") {
          await this.$store.dispatch("signup", formData);
          this.mode = "signup-info";
        } else if (this.mode === "signup-info") {
          this.validateForm();
          if (!this.formIsValid) {
            return;
          }

        

          const signupInfo = {
            email: this.$store.getters.userEmail,

            first: this.firstName.val,
            last: this.lastName.val,
          };
          this.$store.dispatch("members/registerMember", signupInfo);
          this.$router.replace("/");
        }
      } catch (err) {
        console.log(err);
        this.error = err.message || "Failed to authenticate, try again later.";
      }

      this.isLoading = false;

      // this.$router.replace('/')
    },
    switchAuthMode() {
      if (this.mode === "login") {
        this.mode = "signup";
      } else {
        console.log("login");
        this.mode = "login";
      }
    },
    handleError() {
      this.error = null;
    },
  },
};
</script>

<style scoped>
.auth-wrapper {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
}
.auth-card {
  border-radius: 0.25rem;
}
</style>